<template>
    <section>
        <input type="text" v-model="companyUUID">
        <button @click="getAllCompany">
            get company
        </button>
        <ul>
            <li v-for='(res, key) in $store.getters.companyBranding' :key="key">
                <p>{{data.banner_image_url}}</p>
            </li>
        </ul>
    </section>
</template>

<script>

import axios from "axios";
export default {
    data() {
        return {
            companyUUID: ''
        }
    },
    watch() {

    },
    methods: {
        getAllCompany() {
            const BASEURL = 'https://svc.moxiworks.com/service/v1/branding'
            //${this.companyUUID}
            const ENDPOINT = `/company/3350665`

            axios.get(BASEURL+ENDPOINT)
            .then((res) => {
                if(res.data.status === 'success') {
                    const self = this;
                    self.$store.commit('updateCompanyBranding', res.data.data.result_list)
                }
            })
            .catch((err) => {
                console.log(err)
            })
        }
    }



}
</script>

