module.exports = {
  polygon: {
    custom: true,
    layertype: '',
    name: '',
    geojson: {
      type: "MultiPolygon",
      coordinates: [],
    },
    geolayerid: -1,
    templateids: []
  },
  createMultiPolygon(currentLayer, drawnObject) {

    let thsArray, thsArrayTwo;
    
    drawnObject.forEach((o, i) => {
      thsArray = [];
      thsArrayTwo = [];
      
      o.getPath().getArray().forEach((o, i) => {
        
        const lat = parseFloat(o.lat().toString());
        
        const lng = parseFloat(o.lng().toString())
        
        thsArrayTwo.push([lng, lat]);

      });

      // Close the polygon and round out the array.
      thsArrayTwo.push(thsArrayTwo[0]);
      thsArray.push(thsArrayTwo);
      this.polygon.geojson.coordinates.push(thsArray);
    });

    if (!this.validateAndNamePolygon()) {
      this.createLayer();
    }

  },
  validateAndNamePolygon() {
    let err = false;
    let $layerType2 = $("#layerType2");
    let $name = $("input[name=customName]");
    this.polygon.layertype = $layerType2.val();
    this.polygon.name = $name.val();
    
    if ($.trim(this.polygon.layertype) === '') {
      err = true;
      $layerType2.off('change').on('change', function() {
        $(this).closest('.form-group').removeClass('errored');
      }).closest('.form-group').addClass('errored')
    }
    if ($.trim(this.polygon.name) === '') {
      err = true;
      $name.off('keyup').on('keyup', function() {
        $(this).closest('.form-group').removeClass('errored');
      }).closest('.form-group').addClass('errored');
    }
    
    return err;
    
  },
  recreateMultiPolygon(currentLayer) {
    if(!this.validateAndNamePolygon()) {
      currentLayer.forEach((coord) => {
        this.polygon.geojson.coordinates.push(coord);
      })
      this.createLayer();
    }
  },
  createLayer(geoJSON) {
    $.ajax({
      url: '/api/new_layer',
      method: 'post',
      data: { geolayer : JSON.stringify(this.polygon) },
      success: function(res) {
        PubSub.publish('NEW_LAYER_COMPLETE');
        console.log(res)
      }
    })
  },
  deleteLayer(layerid) {
    $.ajax({
      url: `/api/delete_layer/${layerid}`,
      success: function() {
        PubSub.publish('LAYER_DELETED');
      }
    });
  },
  resetPolygonInstance() {
    this.polygon = {
      custom: true,
      layertype: '',
      name: '',
      geojson: {
        type: "MultiPolygon",
        coordinates: [],
      },
      geolayerid: -1,
      templateids: []
    }
  }
}
