<template>
  <section>
    <div id="layer-options">
      <ul>
        <li v-for="(rec, index) in $store.getters.customLayerResponse" v-bind:key="`rec-${index}`" v-on:click="getLayer(rec.geolayerid)">
          Name: {{ rec.name | normalizeBadEncoding }}<br />
          ID: {{ rec.geolayerid }} <br />
          Type: {{ rec.layertype }}
        </li>
      </ul>
    </div>
    <div id="atlas-map"></div>
  </section>
</template>

<script>
  import Atlas from '../contrib/Atlas';
  import Polygon from '../contrib/Polygon';

  export default {
    mounted() {
      // Poll for when atlas node is available
      const interval = setInterval(() => {
        if (document.querySelector('#atlas-map') !== null) {
          clearInterval(interval);
          Atlas.initMap();
          this.getAllCustomLayers();
        }
      });
    },
    methods: {
      initDrawTool() {
        this.drawingManager = new google.maps.drawing.DrawingManager({
          drawingMode: google.maps.drawing.OverlayType.POLYGON,
          drawingControl: true,
          drawingControlOptions: {
            position: google.maps.ControlPosition.TOP_CENTER,
            drawingModes: ['polygon', 'rectangle']
          }
        });
        this.drawingManager.setMap(this.atlasInstance);
      },
      getAllCustomLayers() {
        const self = this;
        $.ajax({
          url: '/api/get_layers_to_manage',
          method: 'POST',
          success: function(res) {
            console.log(res)
            self.$store.commit('updateCustomLayerResponse', res.data.result_list);
          }
        });
      },
      getLayer(layerid) {
        this.$router.push({ name: 'get-layer', params: { layerid: layerid } })
      },
      drawPolygon() {
        const self = this;
        this.clearPolygon();
        google.maps.event.addListenerOnce(this.atlasInstance, 'idle', () => {
          setTimeout(function() {
            Polygon.dynamicallyDrawLayer(this.$store.getters.currentCustomLayer, window.atlasInstance);
          }, 250)
        });
        google.maps.event.addListenerOnce(this.drawingManager, "overlaycomplete", function(e) {
          let newShape = {}
          newShape = e.overlay
          newShape.type = e.type
          self.drawnEntities.push(newShape)
        });
      },
      clearPolygon() {
        if (this.drawnEntities.length > 0) {
          this.drawnEntities.forEach(function(o, i) {
            o.setMap(null);
          });
          this.$store.commit('updateDrawnEntities', []);
        }
      }
    }
  }
</script>

<style scoped>
  #layer-options {
    position: absolute;
    top: 0;
    left: 0;
    right: 65%;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
  }
  #atlas-map {
    position: absolute;
    top: 0;
    left: 25%;
    right: 0;
    bottom: 0;
  }

  li {
    padding: 24px;
    cursor: pointer;
    border-bottom: 1px solid #dcdcdc;
  }

  li:first-child {
    margin-top: 0;
  }
</style>
