<template>
  <section>
    <div id="layer-options">
      <div class="v-container">
        <h3 v-html="returnLayerTitle()"></h3>
        <div v-if="$store.getters.drawingManager === null">
          <strong>Geolayer ID: </strong> {{$store.getters.currentCustomLayer.geolayerid}}
          <br />
          <strong>Layer Type: </strong> {{$store.getters.currentCustomLayer.layertype}}
          <hr />
          <p>What would you like to do?</p>
          <button class="btn btn-primary " type="button" v-on:click="initDrawTool()">Replace Layer</button>
          <button class="btn btn-danger" type="button" v-on:click="deleteLayerPreFlight()">Delete Layer</button>
        </div>
        <div v-else-if="$store.getters.drawingManager !== null && !$store.getters.layerDrawn">
          <p>The drawing tool has been activated. Please draw your new layer on the map. Once you've closed your polygon, you'll recieve more options.</p>
        </div>
        <div v-else>
          <br />
          <LayerForm />
          <dl class="form-group">
            <dd>
              <button class="btn" type="button" v-on:click="replaceLayer()">Replace Layer</button>
            </dd>
          </dl>
        </div>
      </div>
    </div>
    <div id="atlas-map"></div>
  </section>
</template>

<script>
  import Store from '../data/Store';
  import Atlas from '../contrib/Atlas';
  import Polygon from '../contrib/Polygon';
  import Service from '../contrib/Services';
  import LayerForm from '../components/LayerForm';

  export default {
    components: {
      LayerForm
    },
    data() {
      return {
        formHidden: true
      }
    },
    mounted() {
      // Poll for when atlas node is available
      const interval = setInterval(() => {
        if (document.querySelector('#atlas-map') !== null) {
          clearInterval(interval);
          Atlas.initMap();
          this.getLayerAndPersist();
        }
      });
    },
    beforeRouteLeave (to, from, next) {
      this.$store.commit('updateDrawingManager', null);
      this.$store.commit('resetNewlyDrawnObject');
      Service.resetPolygonInstance();
      next();
    },
    methods: {
      getLayerAndPersist() {
        const self = this;
        $.ajax({
          url: `/api/get_layer/${this.$route.params.layerid}`,
          method: 'POST',
          success: function(res) {
            self.$store.commit('updateCurrentCustomLayer', res.data.result_list[0]);
            const entities = Polygon.draw(self.$store.getters.currentCustomLayer.geojson, true);
            self.$store.commit('updateDrawnEntities', [entities]);
          }
        });
      },
      initDrawTool() {
        if (this.$store.getters.drawingManager !== null) return false;
        this.clearPolygonEntities();
        const drawingManager = new google.maps.drawing.DrawingManager({
          drawingMode: google.maps.drawing.OverlayType.POLYGON,
          drawingControl: true,
          polygonOptions: {
            editable: true,
            clickable: true
          },
          drawingControlOptions: {
            position: google.maps.ControlPosition.TOP_CENTER,
            drawingModes: ['polygon']
          }
        });
        this.$store.commit('updateDrawingManager', drawingManager);
        this.$store.getters.drawingManager.setMap(window.atlasInstance);
        this.setPolygonListener();
      },
      clearPolygonEntities() {
        this.$store.getters.drawnEntities.forEach((o, i) => {
          o.setMap(null);
        });
      },
      setPolygonListener() {
        google.maps.event.addListener(this.$store.getters.drawingManager, "polygoncomplete", (polygon) => {
          this.$store.commit('updateLayerDrawn', true);
          this.$store.commit('updateNewlyDrawnObject', polygon);
        });
      },
      returnLayerTitle() {
        if (!this.$store.getters.currentCustomLayer.name) return false;
        return this.$options.filters.normalizeBadEncoding(this.$store.getters.currentCustomLayer.name);
      },
      replaceLayer() {
        const getters = this.$store.getters;
        Service.createMultiPolygon(getters.currentCustomLayer, getters.getNewlyDrawnObject);
        PubSub.subscribe('NEW_LAYER_COMPLETE', () => {
          this.deleteLayer(this.$store.getters.currentCustomLayer.geolayerid);
        });
      },
      deleteLayerPreFlight() {
        if (confirm('Are you absolutely sure? This is a big deal.')) {
          this.deleteLayer(this.$store.getters.currentCustomLayer.geolayerid)
        }
      },
      deleteLayer(layerid) {
        const self = this;
        Service.deleteLayer(layerid);
        PubSub.subscribe('LAYER_DELETED', () => {
          self.$router.push({ name: 'all-layers' });
        });
      }
    }
  }
</script>

<style scoped>
  .v-container {
    padding: 24px;
  }
  #layer-options {
    position: absolute;
    top: 0;
    left: 0;
    right: 75%;
    bottom: 0;
    overflow-y: auto;
    overflow-x: hidden;
  } 
  #atlas-map {
    position: absolute;
    top: 0;
    left: 25%;
    right: 0;
    bottom: 0;
  }
</style>
