<template>
    <section>
        <div class="container" v-if="$store.getters.drawingManager === null">
            <input class="form-control" id='input-geocode' v-model="address" type="text" placeholder="Enter Location" @keyup.enter="initMap()">
            <button class="form-control" id="init-draw" @click="initDrawTool()">Enable Draw Tool </button>
        </div>
        <div class="container" v-else-if="$store.getters.drawingManager !== null && !$store.getters.layerDrawn">
            
        </div>
        <div class="wrapper" v-else>
            <LayerForm />
            <dl class="form-group">
                <dd>
                    <button class="form-control" type="button" @click="createNewLayer()">create</button>
                </dd>
            </dl>
        </div>
        <div id="atlas-map"></div>
    </section>
</template>

<script>
  import Store from '../data/Store';
  import Atlas from '../contrib/Atlas';
  import Polygon from '../contrib/Polygon';
  import Service from '../contrib/Services';
  import LayerForm from '../components/LayerForm';
export default {
    components: {
        LayerForm
    },
    data() {
        return {
            address: '',
        }
    },
    mounted() {
      // Poll for when atlas node is available
      const interval = setInterval(() => {
        if (document.querySelector('#atlas-map') !== null) {
          clearInterval(interval);
          Atlas.initMap();
        }
      });
    },
    beforeRouteLeave (to, from, next) {
      this.$store.commit('updateDrawingManager', null);
      this.$store.commit('resetNewlyDrawnObject');
      Service.resetPolygonInstance();
      next();
    },    
    methods: {
        initMap() {
            const map = window.atlasInstance;
            const geocoder = new google.maps.Geocoder();
            this.findLocation(geocoder, map);
        },
        findLocation(geocoder, map) {
            geocoder.geocode({
                'address': this.address
            }, (res, status) => {
                if(status == google.maps.GeocoderStatus.OK) {
                    const marker = new google.maps.Marker({
                        position: res[0].geometry.location,
                        animation: google.maps.Animation.DROP
                    });
                    map.setCenter(res[0].geometry.location);
                    marker.setMap(map);
                    this.updateViewPort(res[0]);
                };
            });
        },
        updateViewPort(location) {
            window.atlasInstance.fitBounds(location.geometry.viewport);
        },
        initDrawTool() {
            if (this.$store.getters.drawingManager !== null) return false;
            this.clearPolygonEntities();
            const drawingManager = new google.maps.drawing.DrawingManager({
                drawingMode: google.maps.drawing.OverlayType.POLYGON,
                drawingControl: true,
                polygonOptions: {
                    editable: true,
                    draggable: true
                },
                drawingControlOptions: {
                    position: google.maps.ControlPosition.TOP_CENTER,
                    drawingModes: ['polygon']
                }
            });
            this.$store.commit('updateDrawingManager', drawingManager);
            this.$store.getters.drawingManager.setMap(window.atlasInstance);
            this.setPolygonListener();
        },
        clearPolygonEntities() {
            this.$store.getters.drawnEntities.forEach((o, i) => {
                o.setMap(null);
            });
        },
        setPolygonListener() {
            google.maps.event.addListener(this.$store.getters.drawingManager, 'polygoncomplete', (polygon) => {
                this.$store.commit('updateLayerDrawn', true);
                this.$store.commit('updateNewlyDrawnObject', polygon);
            });
        },
        createNewLayer() {
            const getters = this.$store.getters;
            const self = this;
            Service.createMultiPolygon(null, getters.getNewlyDrawnObject);
            PubSub.subscribe('NEW_LAYER_COMPLETE', () => {
                self.$router.push({ name: 'all-layers' });
            });
        }
    }
}
</script>

<style scoped>
.container {
    width: 100%;
    z-index: 100;
    position: relative;
}
.wrapper {
    z-index: 100;
    position: relative;
    width: 20%;
    margin: 10px;
    padding: 10px;
    background-color:rgba(255,255,255,0.7);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
#atlas-map {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}
#input-geocode {
    background-color: #fff;
    font-size: 15px;
    font-weight: 300;
    margin: 12px;
    padding: 0 11px 0 13px;
    text-overflow: ellipsis;
    width: 350px;
}
.form-control {
    background-color: #fff;
    border: 1px solid #999;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
}
</style>