import Vue from 'vue';
import Vuex, { Store } from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
   drawingManager: null,
   drawnEntities: [],
   customLayerResponse: null,
   currentCustomLayer: {},
   layerDrawn: false,
   newlyDrawnObject: [],
   allBoundaries: {},
   drawnBoundaries: [],
   companyBranding: []
  },
  mutations: {
    updateDrawingManager(store, val) {
      store.drawingManager = val;
    },
    updateDrawnEntities(store, val) {
      store.drawnEntities = val;
    },
    updateCustomLayerResponse(store, val) {
      store.customLayerResponse = val;
    },
    updateCurrentCustomLayer(store, val) {
      store.currentCustomLayer = val;
    },
    updateLayerDrawn(store, val) {
      store.layerDrawn = val;
    },
    updateNewlyDrawnObject(store, obj) {
      store.newlyDrawnObject.push(obj);
    },
    resetNewlyDrawnObject(store) {
      store.newlyDrawnObject = [];
    },
    updateAllBoundaries(store, val) {
      store.allBoundaries = val
    },
    updateDrawnBoundaries(store, val) {
      store.drawnBoundaries = val
    },
    updateCompanyBranding(store, val) {
      store.companyBranding = val
    }
  },
  getters: {
    drawingManager: (store) => store.drawingManager,
    drawnEntities: (store) => store.drawnEntities,
    customLayerResponse: (store) => store.customLayerResponse,
    currentCustomLayer: (store) => store.currentCustomLayer,
    getNewlyDrawnObject: (store) => store.newlyDrawnObject,
    layerDrawn: (store) => store.layerDrawn,
    allBoundaries: (store) => store.allBoundaries,
    companyBranding: (store) => store.companyBranding
  },
});
