var Polygon = (function() {

  'use strict';

  var polygon = {
    preserveZoom: false,
    draw: function(geoJSON, drawToMap=true) {
      this.clearPolygon();
      var map = window.atlasInstance
      var paths = this.createGeoJsonPolygon(geoJSON)
      var strokeColor = '#484745';
      var polygon_options = {
        paths: paths,
        strokeColor: strokeColor,
        strokeOpacity: 1,
        strokeWeight: 3,
        fillColor: strokeColor,
        fillOpacity: 0.05
      };
      this.preserveZoom = true;
      this.polygon = new google.maps.Polygon(polygon_options);
      var bounds = this.getPolygonMBR(this.polygon);
      var loc = bounds.getCenter();
      if (drawToMap) this.polygon.setMap(map);
      map.fitBounds(bounds);
      return this.polygon;
    },
    createGeoJsonPolygon: function(geoJSON) {
      var coords, paths;
      var self = this;
      self.polygonPointLocations = [];
      coords = geoJSON.coordinates;
      paths = [];
      
      $.each(coords, function(i, n) {
        return $.each(n, function(j, o) {
          var path;
          path = [];
          $.each(o, function(k, p) {
            var ll;
            ll = new google.maps.LatLng(p[1], p[0]);
            path.push(ll);
            return self.polygonPointLocations.push(ll);
          });
          return paths.push(path);
        });
      });
      return paths;
    },
    getPolygonMBR: function(polygon) {
      var bounds;
      bounds = new google.maps.LatLngBounds();
      polygon.getPaths().forEach(function(p, i) {
        return p.forEach(function(n, j) {
          return bounds.extend(n);
        });
      });
      return bounds;
    },
    clearPolygon: function() {
      if (this.polygon != null) {
        this.polygon.setMap(null);
      }
    }
  };
  
  return polygon;

})();

module.exports = Polygon;
