import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home';
import AllLayers from './views/AllLayers';
import Layer from './views/Layer';
import GeoInspect from './views/GeoInspect';
import NewLayer from './views/NewLayer';
import Brand from './views/Brand';

Vue.use(Router);

// Secret sauce to ignore custom lit-element vitruvius components.
Vue.config.ignoredElements = [/vp-\w*/];

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/layers/all',
      name: 'all-layers',
      component: AllLayers
    },
    {
      path: '/layers/:layerid',
      name: 'get-layer',
      component: Layer
    },
    {
      path: '/geoinspect',
      name: 'geoinspect',
      component: GeoInspect
    },
    {
      path: '/new',
      name: 'new-layer',
      component: NewLayer
    },
    {
      path: '/brand',
      name: 'brand',
      component: Brand
    }
  ]
})
