exports.push([module.id, "\n#atlas-map[data-v-7b5cc03a] {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  top: 0;\n  right: 0;\n}\nli.active[data-v-7b5cc03a] {\n  background-color:rgba(221, 220, 220, 0.699);\n  padding: 0px 10px;\n}\n.main-container[data-v-7b5cc03a] {\n    z-index: 100;\n    position: relative;\n    width: 20%;\n    margin: 10px;\n    padding: 10px;\n    background-color:rgba(255,255,255,0.75);\n    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);\n}\n#input-geocode[data-v-7b5cc03a] {\n    background-color: #fff;\n    font-size: 15px;\n    font-weight: 300;\n    margin: 12px 0;\n    padding: 0 11px 0 13px;\n    text-overflow: ellipsis;\n    width: 100%;\n}\n.form-control[data-v-7b5cc03a] {\n    background-color: #fff;\n    padding: 5px;\n    border: 1px solid #999;\n    line-height: 30px;\n    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);\n}\n#resultsTable[data-v-7b5cc03a] {\n  width: 100%;\n  height: 700px;\n  overflow-y: auto;\n  overflow-x: hidden;\n}\n#layer-form[data-v-7b5cc03a] {\n  width: 100%;\n  position: absolute;\n  left: 100%;\n  z-index: 100;\n  top: 0%;\n}\n", "",{"version":3,"sources":["/Users/timmuangkeo/Desktop/fork/LayerDraw copy/app/javascript/views/app/javascript/views/GeoInspect.vue"],"names":[],"mappings":";AAiIA;EACA,kBAAA;EACA,OAAA;EACA,SAAA;EACA,MAAA;EACA,QAAA;AACA;AACA;EACA,2CAAA;EACA,iBAAA;AACA;AACA;IACA,YAAA;IACA,kBAAA;IACA,UAAA;IACA,YAAA;IACA,aAAA;IACA,uCAAA;IACA,wCAAA;AACA;AACA;IACA,sBAAA;IACA,eAAA;IACA,gBAAA;IACA,cAAA;IACA,sBAAA;IACA,uBAAA;IACA,WAAA;AACA;AACA;IACA,sBAAA;IACA,YAAA;IACA,sBAAA;IACA,iBAAA;IACA,wCAAA;AACA;AACA;EACA,WAAA;EACA,aAAA;EACA,gBAAA;EACA,kBAAA;AACA;AACA;EACA,WAAA;EACA,kBAAA;EACA,UAAA;EACA,YAAA;EACA,OAAA;AACA","file":"GeoInspect.vue?vue&type=style&index=0&id=7b5cc03a&scoped=true&lang=css&","sourcesContent":["<template>\n    <section>\n      <div class=\"main-container\">\n        <div id=\"container-geocode\">\n          <h3>Search for Boundaries</h3>\n          <p>Click to view the layer. </p>\n            <input class=\"form-control\" id='input-geocode' v-model=\"search\" type=\"text\" placeholder=\"Enter location\" @keyup.enter=\"initMap()\">\n        </div>\n        <div id=\"listing\" v-if=\"$store.getters.allBoundaries.length\">\n          <div id=\"resultsTable\">\n            <ul>\n              <li v-for=\"(rec, index) in $store.getters.allBoundaries\" :class=\"{active:index == selected}\" v-bind:key=\"`rec-${index}`\" @click=\"getLayer(rec); selected = index\">\n                <strong>GeoLayer ID:</strong> {{rec.geolayerid}}\n                <br/> \n                <strong>Name:</strong> {{rec.name | normalizeBadEncoding}}\n                <br/> \n                <strong>layertype:</strong> {{rec.layertype}}\n                <br/> \n                <strong>custom: {{rec.custom}}</strong> \n                <br/> \n                <button class=\"form-control\" id=\"input-geocode\" @click=\"formHidden = !formHidden\">Duplicate</button>\n                <hr>\n              </li>\n            </ul>\n          </div>\n        </div>\n          <div class=\"main-container\" id=\"layer-form\" v-if=\"!formHidden\">\n            <h4>Enter a Name and Type.</h4>\n            <p>Then click <strong>Duplicate</strong> once more</p>\n            <strong>Note:</strong>\n            <p>Remember to include prefix and suffix if the address contains them (Pl, Dr, County...)</p>\n            <LayerForm/>\n            <button class=\"form-control\" @click=\"duplicateLayer(rec)\">Submit</button>\n          </div>\n      </div>\n      <div id=\"atlas-map\"></div>    \n    </section>\n</template>\n\n<script>\nimport Store from '../data/Store';\nimport Atlas from '../contrib/Atlas';\nimport Polygon from '../contrib/Polygon';\nimport Service from '../contrib/Services';\nimport LayerForm from '../components/LayerForm';\nimport { createNamespacedHelpers } from 'vuex';\n\n\nexport default {\n  components: {\n    LayerForm\n  },\n  data() {\n    return {\n      search: '',\n      selected: null,\n      formHidden: true\n    }\n  },\n  mounted() {\n    // Poll for when atlas node is available\n    const interval = setInterval(() => {\n      if (document.querySelector('#atlas-map') !== null) {\n        clearInterval(interval);\n        Atlas.initMap();\n      }\n    });\n  },\n  beforeRouteLeave (to, from, next) {\n    this.$store.commit('updateAllBoundaries', {})\n    next()\n  },\n  methods: {\n    initMap() {\n      const map = window.atlasInstance;\n      const geocoder = new google.maps.Geocoder()\n      this.findLocation(geocoder, map)\n    },\n    // geocode location and push result's lat/lng to get all layers\n    findLocation(geocoder, map) {\n      geocoder.geocode({\n        'address': this.search\n      }, (res, status) => {\n        console.log(res)\n        if(status == google.maps.GeocoderStatus.OK) {\n          const marker = new google.maps.Marker({\n            position: res[0].geometry.location,\n            animation: google.maps.Animation.DROP\n          })\n          map.setCenter(res[0].geometry.location)\n          marker.setMap(map);\n          this.updateViewPort(res[0]);\n          this.getAllLayerPoint(res[0]);\n        }\n      })\n    },\n    updateViewPort(res) {\n      window.atlasInstance.fitBounds(res.geometry.viewport);\n    },\n    // gett all the layers\n    getAllLayerPoint(res) {\n      const self = this;\n      let lat = res.geometry.location.lat();\n      let lng = res.geometry.location.lng();\n      $.ajax({\n        url: window.base_service + `/listing/geo/layer/search?center_lat=${lat}&center_lon=${lng}&geotype=all&buffer_miles=0`,\n        success: function(val) {\n          self.$store.commit('updateAllBoundaries', val.data.result_list);\n        }\n      })\n    },\n    // update store with current custom layer's geojson\n    getLayer(result) {\n      this.$store.commit('updateCurrentCustomLayer', result)\n      Polygon.draw(result.geojson, true);\n    },\n    // copy the current layer's geojson coordinates\n    duplicateLayer(rec) {\n      Service.resetPolygonInstance();\n      Service.recreateMultiPolygon(this.$store.getters.currentCustomLayer.geojson.coordinates);\n      PubSub.subscribe('NEW_LAYER_COMPLETE', () => {\n        this.$router.push({ name: 'all-layers' }).catch('err');\n      });\n    }\n  }\n}\n</script>\n\n<style scoped>\n#atlas-map {\n  position: absolute;\n  left: 0;\n  bottom: 0;\n  top: 0;\n  right: 0;\n}\nli.active {\n  background-color:rgba(221, 220, 220, 0.699);\n  padding: 0px 10px;\n}\n.main-container {\n    z-index: 100;\n    position: relative;\n    width: 20%;\n    margin: 10px;\n    padding: 10px;\n    background-color:rgba(255,255,255,0.75);\n    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);\n}\n#input-geocode {\n    background-color: #fff;\n    font-size: 15px;\n    font-weight: 300;\n    margin: 12px 0;\n    padding: 0 11px 0 13px;\n    text-overflow: ellipsis;\n    width: 100%;\n}\n.form-control {\n    background-color: #fff;\n    padding: 5px;\n    border: 1px solid #999;\n    line-height: 30px;\n    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);\n}\n#resultsTable {\n  width: 100%;\n  height: 700px;\n  overflow-y: auto;\n  overflow-x: hidden;\n}\n#layer-form {\n  width: 100%;\n  position: absolute;\n  left: 100%;\n  z-index: 100;\n  top: 0%;\n}\n</style> "]}]);

